<template>
  <el-dialog
    title=""
    :visible="pickBgm.status"
    width="50%"
    :before-close="handleCancel"
    custom-class="bgm-dialog">
    <el-tabs 
      tab-position="left" 
      v-model="activeName" 
      style="min-height: 200px;" 
      @tab-click="handleClick">
      <el-tab-pane label="" name="public">
        <bgm-lists :lists="publicBGMs" @pick="pick" @play="handlePlay" :active="curPickBgm" />
      </el-tab-pane>
      <el-tab-pane label="" name="user">
        <bgm-lists :lists="userBgmLists" @pick="pick" @play="handlePlay" :active="curPickBgm" />
      </el-tab-pane>
    </el-tabs>
    <p v-show="curPickBgm.src" class="bgm-active">{{ curPickBgm.name }}</p>
    <audio ref="music" preload="auto" loop></audio>
    <span slot="footer" class="dialog-footer">
      <el-button @click="handleCancel"> </el-button>
      <el-button type="primary" @click="handleConfirm"> </el-button>
    </span>
  </el-dialog>
</template>

<script>
import lists from './lists.vue'
import { getPublicBGMs, getUserBGMs } from '@/api/materials'
export default {
  name: 'PickBgMusic',
  data () {
    return {
      isPlay: false,
      activeName: 'public',
      curPickBgm: {
        id: '',
        name: '',
        src: ''
      },
      userBgmLists: [],
      publicBGMs: []
    }
  },
  computed: {
    pickBgm () {
      return this.$store.getters.pickBgm
    }
  },
  methods: {
    handleClick () {
      const activeName = this.activeName
      if (activeName === 'user' && this.hasFetchData === false) {
        getUserBGMs().then(
          (res) => {
            this.hasFetchData = true
            this.userBgmLists = res.lists
          },
          (err) => {
            // this.userBgmStatus = false
            this.$notify.error({
              title: '',
              message: err
            })
          }
        )
      }
    },
    handlePlay (bgm) {
      const music = this.$refs.music
      if (this.isPlay && (music.src === bgm.src)) {
        this.isPlay = false
        music.pause()
      } else {
        this.isPlay = true
        music.src = bgm.src
        music.play()
      }
    },
    pick (bgm) {
      Object.assign(this.curPickBgm, bgm)
    },
    handleCancel () {
      if (this.isPlay) {
        this.$refs.music.pause()
      }
      this.curPickBgm.id = ''
      this.$store.commit('SET_PICK_BGM', false)
    },
    handleConfirm () {
      this.pickBgm && this.pickBgm.callback && this.pickBgm.callback(this.curPickBgm)
      this.handleCancel()
    }
  },
  mounted () {
    this.hasFetchData = false
    getPublicBGMs().then(
      (res) => {
        this.publicBGMs = res.lists
      },
      (err) => {
        // this.publicBgmStatus = false
        this.$notify.error({
          title: '',
          message: err
        })
      }
    )
  },
  components: {
    bgmLists: lists
  }
}
</script>



